<template>
<div class="col-md-12 col-sm-12 col-xs-12">  
  <b-row>
   <div class="col-md-1 col-sm-1 col-xs-12"></div>    
   <div class="col-md-10 col-sm-10 col-xs-12" style="margin-top:150px;"> 
      <div class="col-md-12">
          <H3 class="titulo">NUESTRA ESCUELA</H3>
           <hr class="hr"/>
      </div>
   </div>      
   <div class="col-md-1 col-sm-1 col-xs-12"></div>
  </b-row>
  <b-row>
    <div class="col-md-1 col-sm-1 col-xs-12"></div>  
    <div class="col-md-6 col-sm-6 col-xs-12">
       <b-carousel id="carousel-fade" style="text-shadow: 0px 0px 2px #000;border-radius:9px;" :interval="2000"  fade indicators >          
      <b-carousel-slide class="d-block img-fluid w-100" v-for="item in school.images" :key="item.id" :caption="item.title">
        <template v-slot:img>
                <img
                class="d-block class-name"
                width="100%"
                height="650px"      
                :src="item.url"
                style="color:item.colorTitle"
                alt="image slot"/>
        </template>
      </b-carousel-slide>     
     </b-carousel>         
    </div>    
    <div class="col-md-5 col-sm-5 col-xs-12"></div>      
     
  
 
  </b-row>
   
</div>

</template>

<script>
 import axios from 'axios'
  export default {
    data() {
      return {
         school:{title:'',images:[]}         
      }
    },
    created()
        {
            this.getCovers();            
        },     
    methods:{
      getCovers()
        {
            console.log('listando imagenes de la escuela');
            let me=this;
            let header={"Authorization" : "Bearer " + this.$store.state.token};
            let configuration= {headers : header};        
            axios.get('/api/v1/schools/images/web',configuration).then(function(response)
                {                                                           
                    me.school=response.data;
                    console.log(me.school);
                }).catch(function(error){
                        console.log(error);
                });
        }
    }
  }
</script>
