<template>
 
  <div class="container-fluid" >
       <br/><br/><br/><br/>
    <div class="col-md-12 col-xs-12 col-sm-12">
        <h1 class="titulo">NUESTROS SERVICIOS </h1>
        <hr class="hr"/>
    </div>
    <br/><br/>
    <b-row >
      <b-col cols="2"></b-col>
      <b-col cols="10">
        <b-col cols="12" v-for="item in lServices" :key="item.id">         
                <div class="col-md-4 col-sm-4 col-xs-12 drop-shadow raised" style="margin-bottom:15px;margin-left:20px; " >           
                  <b-row>
                      <b-col cols="4">
                          <b-img  :src="item.image" width="150" height="150"/>                        
                      </b-col>
                      <b-col cols="8">
                            <h4>{{item.name}}</h4>    
                          <span>
                          {{item.description}}
                          </span>                                               
                      </b-col>                                                                                                                 
                  </b-row>  
                  <b-row>
                    <b-col cols="10"></b-col>
                    <b-col cols="2" v-show="item.content.length>0">
                       <b-button type="link" variant="warning" @click="viewService(item)">más info</b-button> 
                    </b-col>
                  </b-row>                                    
                </div>                           
            </b-col>
      </b-col>    
    </b-row>
  </div> 
</template>

<script>
import axios from 'axios'
  export default {
    data() {
      return {
      lServices:[]
      }
    },
    created()
        {
            this.getServices();          
            
        }, 
    methods:{ 
    viewService(item)
        {
          console.log(item);
            this.$router.push({name:'ServiceView',params:{services:item}});
        },      
      getServices()
      {
       let me=this;
            
      let header={"Authorization" : "Bearer " + this.$store.state.token};
      let configuration= {headers : header}; 
                
            axios.get('/api/v1/masters/services', configuration).then(function(response)
              {                                                                  
                me.lServices=response.data;
               
                console.log(me.lServices);
              }).catch(function(error){
                    console.log("ERROR :"+ error);
              });
      }
    }    
  }
</script>
<style>
 .raised{
		-webkit-box-shadow: 0 15px 10px -10px rgba(0, 0, 0, 0.5), 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
		-moz-box-shadow: 0 15px 10px -10px rgba(0, 0, 0, 0.5), 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
				box-shadow: 0 15px 10px -10px rgba(0, 0, 0, 0.5), 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
}  

.drop-shadow {
		position:relative;
		float:left;
		width:40%;
		padding:1em;
		margin:2em 10px 4em;
		background:#fff;
		-webkit-box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
		-moz-box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
		box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
	}
</style>