import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import { BootstrapVue,AvatarPlugin,BootstrapVueIcons  } from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import AnimatedVue from 'animated-vue'
import * as VueGoogleMaps from 'vue2-google-maps'
import 'froala-editor/js/plugins.pkgd.min.js';
//Import third party plugins
import 'froala-editor/js/third_party/embedly.min';
import 'froala-editor/js/third_party/font_awesome.min';
import 'froala-editor/js/third_party/spell_checker.min';
import 'froala-editor/js/third_party/image_tui.min';
import VueSignaturePad from 'vue-signature-pad';
// Import Froala Editor css files.
import 'froala-editor/css/froala_editor.pkgd.min.css';
import VueFroala from 'vue-froala-wysiwyg'
import AOS from 'aos'


Vue.config.productionTip = false
axios.defaults.baseURL='https://heraclito-mis-garabatos.azurewebsites.net'
//axios.defaults.baseURL='https://localhost:44337'
Vue.use(BootstrapVue)
Vue.use(AvatarPlugin)
Vue.use(AnimatedVue)
Vue.use(BootstrapVueIcons)
Vue.use(VueSignaturePad)
Vue.use(VueFroala)
Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyDvEKAJWhVLKEhDBwoil5KCKvwS00LBEzs',
    libraries: "places" 
  }
})
 
Vue.use(AOS)
new Vue({
  router,
  store,  
  VueFroala,
  render: h => h(App)
}).$mount('#app')
