      <br/><br/><br/><br/>
    </b-col>
  </b-row>
  <b-row>
      <b-col cols="1">  </b-col>
      <b-col cols="12" xl="3" lg="3" md="5" sm="11" xs="11">
          <img src="../assets/images/MisGarabatos-5.jpg" alg="Mis Garabatos" style="width:90%;height:100%;"/>
      </b-col>
       <b-col cols="12" xl="6" lg="6" md="4" sm="12" xs="12">
          <H3 class="titulo">{{project.weTittle}}</H3>          
          <hr class="hr"/>
          <br/>  <br/>
          <froalaView v-model="project.weContent"></froalaView>
       </b-col>       
  </b-row>
  <b-row><b-col><br><BR/></b-col></b-row>
  <b-row>
    <b-col cols="1">  </b-col>
       <b-col cols="11" xl="6" lg="6" md="12" sm="12" xs="12">
